@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movie-card{position:relative;height:19em;width:13em;margin:1em 1.5em;background-color:#fdfffc;transition:all ease .25s}:hover>.movie-card{transform:scale(1.1)}.homepage-img{height:100%;width:100%}.movie-card-title{opacity:0;transition:all ease .5s;background:#000;background:linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, black 35%);color:#fdfffc;padding:1.8em 0em 0em 0em;position:absolute;bottom:0;width:100%;height:30%;font-size:1em;display:flex;justify-content:center;align-items:center}:hover>.movie-card-title{opacity:.9}
.movies-list{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;background:#011627;background:linear-gradient(180deg, #011627 0%, #093b39 100%)}.section-heading{font-size:2em;color:#fdfffc;margin:1em 20% 1em 20%;border-bottom:#fdfffc 1px solid}
.navbar{border-bottom:#2ec4b6 solid 2px;color:#fdfffc;position:fixed;display:flex;align-items:center;justify-content:center;height:3em;background-color:#011627;top:0;width:100%;z-index:5}.input-box{outline:none;background:#fdfffc url(data:image/png;base64,) no-repeat 5px center;border:solid 1px #ccc;padding-left:2px;width:30px;height:30px;color:transparent;cursor:pointer;border-radius:10em;z-index:99;transition:all .5s;position:relative;z-index:5}.input-box:hover{background-color:#fff;border-color:#2ec4b6;box-shadow:0px 0px 1px 1px #2ec4b6;z-index:99}.input-box::-webkit-input-placeholder{color:transparent;z-index:99}.input-box:focus{width:200px;padding-left:32px;color:#000;background-color:#fff;border-color:#2ec4b6;box-shadow:0px 0px 1px 1px #2ec4b6;z-index:99}.input-box:focus::-webkit-input-placeholder{color:#4a4a4a}.search-wrapper{position:fixed;right:10px;z-index:99}@media only screen and (max-width: 700px){.input-box:focus{width:130px}}@media only screen and (max-width: 500px){.input-box:focus{width:90px}}@media only screen and (max-width: 400px){.input-box{width:20px;height:20px;float:right;background:#fdfffc url(data:image/png;base64,) no-repeat 1px center}.input-box:focus{width:50px}.input-box:focus::-webkit-input-placeholder{color:#4a4a4a;font-size:.6em}.navbar{font-size:.6em;margin:0;width:400px}}
.error-page{height:100vh;color:#fdfffc;width:100%;display:flex;flex-direction:column;color:#fdfffc;justify-content:center;position:relative}.error-header{z-index:1;position:absolute;top:12em;left:12em}.error-text{z-index:1;top:20em;left:10em;position:absolute}.error-tomato{position:absolute;height:400px;width:500px;z-index:-1;top:2em;opacity:.3;-webkit-filter:blur(3px);filter:blur(3px);right:10em}
.movie-preview-page{min-height:100vh;height:100%;width:100%;margin:0;background-position:center;background-attachment:fixed;background-repeat:no-repeat;background-size:cover;display:flex;align-items:center;justify-content:center;overflow:hidden}.description-wrapper{width:90%;height:auto;display:flex;align-items:center;position:relative}.preview-react-player{margin:1em 0 5em 0}.preview-wrapper{width:80%;margin:5em;position:relative;background:rgba(253,255,252,.7);display:flex;flex-direction:column;align-items:center}.movie-preview-img{height:25em;margin:3em}.movie-description{height:100%;width:50em;margin:3em;display:flex;flex-direction:column;align-items:flex-start}.movie-preview-title{font-size:2em;border-bottom:#011627 solid 2px;margin-bottom:.5em}.movie-info-detail{text-align:-webkit-auto;margin:.3em .3em .3em 0}.error-message{height:100vh;color:#fdfffc;width:100%;position:fixed;display:flex;flex-direction:column;justify-content:center}.movie-preview-button{position:absolute;margin:2em;right:0px;top:0px;width:32px;height:32px;background:transparent;border:0px solid transparent;transition:all .25s}.movie-preview-button:hover{-webkit-filter:invert(0.3);filter:invert(0.3);transform:scale(1.1);cursor:pointer}@media only screen and (max-width: 1000px){.movie-preview-img{height:18em;margin:2.5em}.movie-preview-title{font-size:1.5em}.preview-react-player{transform:scale(0.9)}}@media only screen and (max-width: 900px){.movie-preview-img{height:16em}.movie-info-detail{font-size:.8em}.movie-preview-title{font-size:1em}.preview-react-player{transform:scale(0.7);margin:.2em 0 3em 0}}@media only screen and (max-width: 800px){.movie-description{margin:1em}.movie-preview-img{height:14em;margin:2.5em 2em 0 1em}.movie-info-detail{font-size:.7em}.movie-preview-title{font-size:.8em;margin:2.5em .3em .3em 0}.preview-react-player{transform:scale(0.7);margin:.2em 0 3em 0}.trailer-header{margin:.2em}}@media only screen and (max-width: 650px){.movie-preview-img{margin:2em}.preview-react-player{transform:scale(0.5);margin:0em}.description-wrapper{flex-direction:column;width:70%}.movie-description{height:auto;width:auto;flex-direction:column}.trailer-header{font-size:1em;margin-top:2em}.movie-preview-button{margin:0;right:-10px;top:10px;width:10px;height:10px}}@media only screen and (max-width: 360px){.movie-preview-img{height:14em;margin:2em}.movie-info-detail{font-size:.7em}.movie-preview-title{font-size:.8em;margin-top:0}.movie-preview-trailer-section{display:contents;margin:auto}.preview-react-player{transform:scale(0.4);margin:0em}.movie-preview-button{right:0px;top:10px;width:6px;height:6px}}
.player-wrapper{position:relative;padding-top:41.7%;z-index:0;height:100%;width:100%;overflow:hidden;margin-top:3em}.react-player{transform:scale(1.35);position:absolute;top:0;left:0;object-fit:cover;z-index:0}.player-cover{transform:scale(1.35);position:absolute;top:0;height:100%;width:100%;z-index:2}.trailer-info{position:absolute;padding:2em;color:#fdfffc;z-index:1;display:flex;flex-direction:column;align-items:flex-end;top:25%;right:10%;background-color:rgba(0,0,0,.55);box-shadow:0px 0px 60px 40px rgba(0,0,0,.6);border-radius:2em}.trailer-movie-title{border-bottom:#fdfffc 1px solid;margin-bottom:.5em;font-size:4em}.trailer-details{font-size:1.5em}@media only screen and (max-width: 992px){.trailer-movie-title{font-size:3em}.trailer-details{font-size:1em}.react-player{transform:scale(1.7)}.player-wrapper{margin-top:3em}}@media only screen and (max-width: 700px){.trailer-movie-title{font-size:2em}.trailer-details{font-size:.5em}}@media only screen and (max-width: 500px){.trailer-movie-title{font-size:1.3em}.trailer-details{font-size:.4em}}
*{margin:0px}html{height:100%;width:100%;background-color:#011627}.app{text-align:center;background-color:#011627;height:100%;margin:0px;font-family:"Quicksand",sans-serif}#blockColorblindContent{display:none !important}
