@import '../_variables.scss';

.navbar {
  border-bottom: $light-blue solid 2px;
  color: $white;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3em;
  background-color: $dark-blue;
  top: 0;
  width: 100%;
  z-index: 5;
}

.input-box {
  outline: none;
  background: $white url('../assets/search_icon.png') no-repeat 5px center;
  border: solid 1px #ccc;
  padding-left: 2px;
  width: 30px;
  height: 30px;
  color: transparent;
  cursor: pointer;
  -webkit-border-radius: 10em;
  -moz-border-radius: 10em;
  border-radius: 10em;
  z-index: 99;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  position: relative;
  z-index: 5;
}

.input-box:hover {
  background-color: #fff;
  border-color: $light-blue;
  box-shadow: 0px 0px 1px 1px $light-blue;
  z-index: 99;
}

.input-box::-webkit-input-placeholder {
  color: transparent;
  z-index: 99;
}

.input-box:focus {
  width: 200px;
  padding-left: 32px;
  color: #000;
  background-color: #fff;
  border-color: $light-blue;
  box-shadow: 0px 0px 1px 1px $light-blue;
  z-index: 99;

  &::-webkit-input-placeholder {
    color: #4a4a4a;
  }
}

.search-wrapper {
  position: fixed;
  right: 10px;
  z-index: 99;
}

@media only screen and (max-width: 700px) {
  .input-box:focus {
    width: 130px;
  }
}

@media only screen and (max-width: 500px) {
  .input-box:focus {
    width: 90px;
  }
}

@media only screen and (max-width: 400px) {
  .input-box {
    width: 20px;
    height: 20px;
    float: right;
    background: $white url('../assets/search_icon.png') no-repeat 1px center;
  }

  .input-box:focus {
    width: 50px;
    &::-webkit-input-placeholder {
      color: #4a4a4a;
      font-size: 0.6em;
    }
  }
  .navbar {
    font-size: 0.6em;
    margin: 0;
    width: 400px;
  }
}
